import React, { useState } from "react";
import { DatePicker } from "@mui/x-date-pickers";
import dayjs from "dayjs";
import "../css/personal-info.css";
import { Common } from "../utility/Common";
import { motion } from "framer-motion";

const fieldLengthObj = {
    name: 5,
    firstName: 10,
    lastName: 10
}

const validFieldBorderColor = "rgba(0, 0, 0, 0.2)";

const PersonalInfo = () => {

    const [ userDetails, setUserDetails ] = useState({
        name: "",
        firstName: "",
        lastName: "",
        email: "",
        dob: "29/09/2004",
        mobile: ""
    });

    const [ fieldState, setFieldState ] = useState({
        name: true,
        firstName: true,
        lastName: true,
        email: true,
        mobile: true
    })

    const handleDateChange = newValue => {
        const dateStr = newValue.$D + "-" + newValue.$M + "-" + newValue.$y
        setUserDetails(prevUserDetails => {
            return {
                ...prevUserDetails,
                dob: dateStr
            }
        })
    }

    const handleFormChange = event => {
        const { name, value } = event.target;

        let fieldStateValue = true;

        if(fieldLengthObj[name] && !Common.checkLength(value, Common.minNameLength, fieldLengthObj[name])) {
            fieldStateValue = false;
        }
        else if(name === "email") {
            if(!Common.isValidMail(value)) {
                fieldStateValue = false;
            }
        }
        if(name === "mobile") {
            if(!Common.isValidMobileNum(value)) {
                fieldStateValue = false;
            }
        }
        setUserDetails(prevDetails => {
            return {
                ...prevDetails,
                [ name ]: value
            }
        });
        setFieldState(prevState => {
            return {
                ...prevState,
                [ name ]: fieldStateValue
            }
        });
    }

    const handleFormSubmit = event => {
        event.preventDefault();
    }
    return (
        <motion.div 
            className="personal-info y-axis-flex"
            initial={ Common.mainElementsFramerVariants.slideFromRight }
            animate={ Common.mainElementsFramerVariants.stay }
            exit={ Common.mainElementsFramerVariants.exit }
            transition={ Common.mainElementsFramerVariants.elemTransition }
        >
            <h1>Personal Info</h1>

            <div className="user-info-page y-axis-flex">
                <div className="user-info-image-container">
                    <div 
                        alt="user"
                        style={{
                            backgroundImage: "url(/app-icon.png)"
                        }}
                        className="user-info-image"
                    ></div>
                </div>
                <form 
                    className="user-account-form x-axis-flex"
                    onSubmit={ handleFormSubmit }
                >
                    <div className="accounts-input-wrapper y-axis-flex">
                        <label>Name</label>
                        <input 
                            name="name"
                            value={ userDetails.name }
                            onChange={ handleFormChange }
                            style={{
                                borderColor: fieldState.name ? validFieldBorderColor 
                                                             : "red"
                            }}
                        />
                    </div>
                    <div className="accounts-input-wrapper y-axis-flex">
                        <label>First Name</label>
                        <input 
                            name="firstName"
                            value={ userDetails.firstName }
                            onChange={ handleFormChange }
                            style={{
                                borderColor: fieldState.firstName ? validFieldBorderColor 
                                                             : "red"
                            }}
                        />
                    </div>
                    <div className="accounts-input-wrapper y-axis-flex">
                        <label>Last Name</label>
                        <input 
                            name="lastName"
                            value={ userDetails.lastName }
                            onChange={ handleFormChange }
                            style={{
                                borderColor: fieldState.lastName ? validFieldBorderColor 
                                                             : "red"
                            }}
                        />
                    </div>
                    <div className="accounts-input-wrapper user-date-wrapper y-axis-flex">
                        <DatePicker 
                            label="Date of Birth" 
                            value={dayjs(userDetails.dob)} 
                            onChange={handleDateChange} 
                            views={["day", "month", "year"]}
                            disableFuture
                            className="user-dob-setting"
                        />
                    </div>
                    <div className="accounts-input-wrapper y-axis-flex">
                        <label>Email</label>
                        <input 
                            name="email"
                            value={ userDetails.email }
                            onChange={ handleFormChange }
                            style={{
                                borderColor: fieldState.email ? validFieldBorderColor 
                                                             : "red"
                            }}
                        />
                    </div>
                    <div className="accounts-input-wrapper y-axis-flex">
                        <label>Mobile No</label>
                        <input 
                            name="mobile"
                            type="number"
                            value={ userDetails.mobile }
                            onChange={ handleFormChange }
                            style={{
                                borderColor: fieldState.mobile ? validFieldBorderColor 
                                                             : "red"
                            }}
                        />
                    </div>
                    <button 
                        className="common-button personal-info-update-button"
                    >Update</button>
                </form>
            </div>
        </motion.div>
    )
}

export default PersonalInfo;