import React, { useEffect }  from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";

import "./css/index.css";

import 'font-awesome/css/font-awesome.min.css';

import { Common } from "./utility/Common";
import { LocalizationProvider } from "@mui/x-date-pickers";
import ProtectedRoutes  from "./components/ProtectedRoutes";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import  Accounts  from "./pages/Accounts";
import Home from "./components/Home";
import PersonalInfo  from "./components/PersonalInfo";
import DeveloperConsole  from "./components/DeveloperConsole";
import ListApps  from "./components/developer-console/ListApps";
import CreateApp  from "./components/developer-console/CreateApp.js";
import Login  from "./components/Login";
import Credentials  from "./pages/Credentials";
import UpdateApp  from "./components/developer-console/UpdateApp";
import SignUp from "./components/SignUp";



const App = () => {

    useEffect(() => {
        window.addEventListener("click", event => {
            const { id } = event.target;
            Common.getCloseOnFocusOutElems().forEach(elem => {
                if(id !== elem.id) {
                    if(!elem.additionalCheckElems || !elem.additionalCheckElems.includes(id)) {
                        elem.closeElem();
                    }
                }
            });
        }, false)
    })
    return (
        <BrowserRouter>
            <LocalizationProvider dateAdapter={AdapterDayjs}>
                <Routes>
                    <Route path="/">
                        <Route path="" element={<ProtectedRoutes />} >
                            <Route path="" element={<Accounts />}>
                                <Route index element={<Home />} />
                                <Route path="personal-info" element={<PersonalInfo />} />
                                <Route path="developer-console" element={<DeveloperConsole />}>
                                    <Route path="list" element={<ListApps />} />
                                    <Route path="create" element={<CreateApp />} />
                                    <Route path="update" element={<UpdateApp />} />
                                </Route>
                            </Route>
                        </Route>
                        <Route path="oauth" element={<Credentials />}>
                            <Route index element={<Login />} />
                            <Route path="sign-up" element={<SignUp />} />
                        </Route>
                    </Route>
                </Routes>
        </LocalizationProvider>
        </BrowserRouter>
    )
}

export default App;